* {                   
    box-sizing: border-box;
}
  
body {
    margin: 0;
    padding: 0;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}
input {
  -moz-user-select: none;
  -ms-user-select: none;
  -o-user-select: none;
  -webkit-user-select: none;
  -webkit-appearance: none;
}

input:active,
input:focus {
  outline: none;
}

button {
  display: inline-block;
  padding: 0;
  border: 0;
  -moz-user-select: none;
  -ms-user-select: none;
  -o-user-select: none;
  -webkit-user-select: none;
  cursor: pointer;
}

button:active,
button:focus {
  outline: none;
}

button::-moz-focus-inner {
  border: 0;
}

ul {
  list-style-type: none;
  margin: 0;
  padding-left: 25px;
}

textarea {
  outline: none;
  -moz-appearance: none;
  -webkit-appearance: none;
  border: none;
  font-family: "Verdana", "Tahoma", "Arial", sans-serif;
}

::selection {
  background-color: #f3e5ab;
}

.EditableItem {
  height: 50px;
}

.enable-select {
  user-select: text; /* supported by Chrome and Opera */
  -webkit-user-select: text; /* Safari */
  -khtml-user-select: text; /* Konqueror HTML */
  -moz-user-select: text; /* Firefox */
  -ms-user-select: text; /* Internet Explorer/Edge */
}

.EditableItem-Text {
  height: 100%;
  width: 150px;
  border: 2px solid;
  border-color: black;
  padding: 10px;
  background-color: white;
  font-size: 10pt;
}

.EditableItem-Button {
  height: 25px;
  width: 25px;
  background-color: white;
  border-right: 1px solid whitesmoke;
  border-bottom: 1px solid whitesmoke;
  color: grey;
  font-size: 10pt;
}

.EditableItem-Button_add:hover {
  color: black;
}

.EditableItem-Button_remove:hover {
  color: orangered;
}

.form-container {
  max-width: 300px;
  padding: 10px;
  background-color: white;
}

/* The popup form - hidden by default */
.form-popup {
  position: absolute;
  bottom: 0;
  right: 15px;
  border: 3px solid #f1f1f1;
  z-index: 9;
}

/* Add styles to the form container */
.form-container {
  max-width: 300px;
  padding: 10px;
  background-color: white;
}

/* Full-width input fields */
.form-container input[type="text"],
.form-container input[type="password"] {
  width: 100%;
  padding: 15px;
  margin: 5px 0 22px 0;
  border: none;
  background: #f1f1f1;
}

/* When the inputs get focus, do something */
.form-container input[type="text"]:focus,
.form-container input[type="password"]:focus {
  background-color: #ddd;
  outline: none;
}

/* Set a style for the submit/login button */
.form-container .btn {
  background-color: #04aa6d;
  color: white;
  padding: 16px 20px;
  border: none;
  cursor: pointer;
  width: 100%;
  margin-bottom: 10px;
  opacity: 0.8;
}

/* Add a red background color to the cancel button */
.form-container .cancel {
  background-color: red;
}

/* Add some hover effects to buttons */
.form-container .btn:hover,
.open-button:hover {
  opacity: 1;
}

.TreeNode {
    background: none;
    margin: 3px;
}
.AddButton {
  height: 70px;
  display: absolute;
  align-items: center;
  margin-top: 5px;
}

.AddButton-Inner {
  height: 40px;
  width: 40px;
  border-radius: 100%;
  font-size: 18pt;
  margin-left: 30px;
  background-color: white;
  border: 1px solid gainsboro;
}

.AddButton-Inner:hover:active {
  background-color: rgb(250, 250, 250);
  border: 1px solid rgb(235, 235, 235);
}

.addbutton-text {
  padding-left: 10px;
}

.ControlPanel {
    height: 40px;
    display: flex;
    align-items: center
}

.ControlPanel-Button {
    margin-left: 25px;
    background-color: white;
    font-size: 12pt;
    color: dodgerblue;
    height: 30px;
    border-radius: 5px;

}

.ControlPanel-Button:hover:active {
    opacity: .4;
}

.ControlPanel-Button_load {
    color: mediumturquoise;
}
.TextView {
    width: 100%;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
}

.TextView-Area {
    resize: none;
    width: 90%;
    height: 90%;
    padding-left: 25px;
    color: dimgrey;
    font-size: 8pt;
    font-family: "Consolas", "Lucida Console", "Monaco", "Menlo", monospace;
    border-left: 1px solid gainsboro;
}
.Tree {
  display: flex;
  width: 100%;
  min-width: 800px;
}

.Tree-LeftSide {
  width: 65%;
}

.Tree-RightSide {
  width: 35%;
}

.download-buttons {
  position: relative;
  display: inline-block;
}

